import { extend } from 'flarum/common/extend';
import HeaderPrimary from 'flarum/forum/components/HeaderPrimary';
import app from 'flarum/forum/app';
import PhilosophyPage  from "./components/IdeologyPage";

app.initializers.add('ideology-page', function () {
  //add link to header
  extend(HeaderPrimary.prototype, 'items', function(items) {
    items.add('ideology', <a href="/ideology"><strong>О нас</strong></a>);
  });

  //add route
  app.routes.philosophy = {path: '/ideology', component: PhilosophyPage};
});
